html, body {
  height: 99%;
}

h1 {
  color: #ff69b4;
  font-family: cursive;
}

* {
  box-sizing: border-box;
}

@media only screen and (max-width: 600px) {
  .slideshow-container {
    width: 100%;
  }
}

.slideshow-container {
  border-style: solid;
  width: 80%;
  height: 100%;
  margin: auto;
  position: relative;
  overflow: hidden;
}

.mySlides {
  grid-template-columns: repeat(5, 1fr);
  gap: 10px;
  height: 100%;
  display: none;
}

.slideContainer {
  border-style: solid;
  grid-template-columns: repeat(5, 1fr);
  gap: 10px;
  height: 100%;
  display: grid;
}

.sketchDisplay {
  grid-area: 1 / 1 / auto / 5;
  height: 100%;
  overflow: hidden !important;
}

.sketchDisplayDiv {
  grid-area: 1 / 1 / auto / 5;
  height: 100%;
}

h2 {
  float: right;
}

.prev, .next {
  cursor: pointer;
  color: #fff;
  -webkit-user-select: none;
  user-select: none;
  border-radius: 0 3px 3px 0;
  grid-column: 1 / 5;
  width: auto;
  margin-top: -22px;
  padding: 16px;
  font-size: 18px;
  font-weight: bold;
  transition: all .6s;
  position: absolute;
  top: 50%;
}

.next {
  border-radius: 3px 0 0 3px;
  right: 0;
}

.prev:hover, .next:hover {
  background-color: #000c;
}

.text {
  color: #f2f2f2;
  text-align: center;
  width: 100%;
  padding: 8px 12px;
  font-size: 15px;
  position: absolute;
  bottom: 8px;
}

.numbertext {
  color: #f2f2f2;
  padding: 8px 12px;
  font-size: 12px;
  position: absolute;
  top: 0;
}

.dot {
  cursor: pointer;
  background-color: #bbb;
  border-radius: 50%;
  width: 15px;
  height: 15px;
  margin: 0 2px;
  transition: background-color .6s;
  display: inline-block;
}

.active, .dot:hover {
  background-color: #717171;
}

.fade {
  animation-name: fade;
  animation-duration: 1.5s;
}

@keyframes fade {
  from {
    opacity: .4;
  }

  to {
    opacity: 1;
  }
}

